export function isVar(value) {
    return value.startsWith("--");
}
export function isPrivateVar(value) {
    return value.startsWith("__");
}
export function isSelector(value) {
    return /[^a-z-]/i.test(value) && !isVar(value);
}
export function isGlobalSelector(value) {
    return value.startsWith("@global");
}
export function isMediaQuery(value) {
    return value.startsWith("@media");
}
export function isKeyframes(value) {
    return value.startsWith("@keyframes");
}
