/**
 * Returns a number whose value is limited to the given range.
 * @param {number} value The value to be clamped
 * @param {number} min The lower boundary of the output range
 * @param {number} max The upper boundary of the output range
 * @returns {number} A number in the range [min, max]
 */
function clamp(value, min = 0, max = 1) {
    if (process.env.NODE_ENV !== "production") {
        if (value < min || value > max) {
            console.error(`MUI: The value provided ${value} is out of range [${min}, ${max}].`);
        }
    }
    return Math.min(Math.max(min, value), max);
}
/**
 * Converts a color from CSS hex format to CSS rgb format.
 * @param {string} color - Hex color, i.e. #nnn or #nnnnnn
 * @returns {string} A CSS rgb color string
 */
export function hexToRgb(color) {
    color = color.substr(1);
    const re = new RegExp(`.{1,${color.length >= 6 ? 2 : 1}}`, "g");
    let colors = color.match(re);
    if (colors && colors[0].length === 1) {
        colors = colors.map((n) => n + n);
    }
    return colors
        ? `rgb${colors.length === 4 ? "a" : ""}(${colors
            .map((n, index) => {
            return index < 3
                ? parseInt(n, 16)
                : Math.round((parseInt(n, 16) / 255) * 1000) / 1000;
        })
            .join(", ")})`
        : "";
}
function intToHex(int) {
    const hex = int.toString(16);
    return hex.length === 1 ? `0${hex}` : hex;
}
/**
 * Returns an object with the type and values of a color.
 *
 * Note: Does not support rgb % values.
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla()
 * @returns {object} - A MUI color object: {type: string, values: number[]}
 */
export function decomposeColor(color) {
    // Idempotent
    if (typeof color !== "string") {
        return color;
    }
    if (color.charAt(0) === "#") {
        return decomposeColor(hexToRgb(color));
    }
    const marker = color.indexOf("(");
    const type = color.substring(0, marker);
    if (["rgb", "rgba", "hsl", "hsla", "color"].indexOf(type) === -1) {
        throw new Error("MUI: Unsupported `%s` color.\n" +
            "The following formats are supported: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color(). " +
            color);
    }
    const valuesInput = color.substring(marker + 1, color.length - 1);
    let values;
    let colorSpace;
    if (type === "color") {
        values = valuesInput.split(" ");
        colorSpace = values.shift();
        if (values.length === 4 && values[3].charAt(0) === "/") {
            values[3] = values[3].substr(1);
        }
        if (["srgb", "display-p3", "a98-rgb", "prophoto-rgb", "rec-2020"].indexOf(
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        colorSpace) === -1) {
            throw new Error("MUI: unsupported `%s` color space.\n" +
                "The following color spaces are supported: srgb, display-p3, a98-rgb, prophoto-rgb, rec-2020. " +
                colorSpace);
        }
    }
    else {
        values = valuesInput.split(",");
    }
    return { type, values: values.map((value) => parseFloat(value)), colorSpace };
}
/**
 * Converts a color object with type and values to a string.
 * @param {object} color - Decomposed color
 * @param {string} color.type - One of: 'rgb', 'rgba', 'hsl', 'hsla'
 * @param {array} color.values - [n,n,n] or [n,n,n,n]
 * @returns {string} A CSS color string
 */
export function recomposeColor(color) {
    const { type, colorSpace } = color;
    const { values } = color;
    let newValues;
    if (type.indexOf("rgb") !== -1) {
        // Only convert the first 3 values to int (i.e. not alpha)
        newValues = values
            .map((n, i) => (i < 3 ? parseInt(n.toString(), 10) : n))
            .join(",");
    }
    else if (type.indexOf("hsl") !== -1) {
        newValues = values
            .map((n, i) => (i === 1 || i === 2 ? `${n}%` : n))
            .join(",");
    }
    if (type.indexOf("color") !== -1) {
        newValues = `${colorSpace} ${values.join(" ")}`;
    }
    else {
        newValues = `${values.join(", ")}`;
    }
    return `${type}(${newValues})`;
}
/**
 * Converts a color from CSS rgb format to CSS hex format.
 * @param {string} color - RGB color, i.e. rgb(n, n, n)
 * @returns {string} A CSS rgb color string, i.e. #nnnnnn
 */
export function rgbToHex(color) {
    // Idempotent
    if (color.indexOf("#") === 0) {
        return color;
    }
    const { values } = decomposeColor(color);
    return `#${values
        .map((n, i) => intToHex(i === 3 ? Math.round(255 * n) : n))
        .join("")}`;
}
/**
 * Converts a color from hsl format to rgb format.
 * @param {string} color - HSL color values
 * @returns {string} rgb color values
 */
export function hslToRgb(color) {
    const c = decomposeColor(color);
    const { values } = c;
    const h = values[0];
    const s = values[1] / 100;
    const l = values[2] / 100;
    const a = s * Math.min(l, 1 - l);
    const f = (n, k = (n + h / 30) % 12) => l - a * Math.max(Math.min(k - 3, 9 - k, 1), -1);
    let type = "rgb";
    const rgb = [
        Math.round(f(0) * 255),
        Math.round(f(8) * 255),
        Math.round(f(4) * 255),
    ];
    if (c.type === "hsla") {
        type += "a";
        rgb.push(values[3]);
    }
    return recomposeColor({ type, values: rgb });
}
/**
 * The relative brightness of any point in a color space,
 * normalized to 0 for darkest black and 1 for lightest white.
 *
 * Formula: https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color()
 * @returns {number} The relative brightness of the color in the range 0 - 1
 */
export function getLuminance(color) {
    const { type, values } = decomposeColor(color);
    let rgb = type === "hsl" ? decomposeColor(hslToRgb(color)).values : values;
    rgb = rgb.map((val) => {
        if (type !== "color") {
            val /= 255; // normalized
        }
        return val <= 0.03928 ? val / 12.92 : ((val + 0.055) / 1.055) ** 2.4;
    });
    // Truncate at 3 digits
    return Number((0.2126 * rgb[0] + 0.7152 * rgb[1] + 0.0722 * rgb[2]).toFixed(3));
}
/**
 * Calculates the contrast ratio between two colors.
 *
 * Formula: https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests
 * @param {string} foreground - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla()
 * @param {string} background - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla()
 * @returns {number} A contrast ratio value in the range 0 - 21.
 */
export function getContrastRatio(foreground, background) {
    const lumA = getLuminance(foreground);
    const lumB = getLuminance(background);
    return (Math.max(lumA, lumB) + 0.05) / (Math.min(lumA, lumB) + 0.05);
}
/**
 * Sets the absolute transparency of a color.
 * Any existing alpha values are overwritten.
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color()
 * @param {number} value - value to set the alpha channel to in the range 0 - 1
 * @returns {string} A CSS color string. Hex input values are returned as rgb
 */
export function alpha(color, value) {
    // eslint-disable-next-line prefer-const
    let { type, values } = decomposeColor(color);
    value = clamp(value);
    if (type === "rgb" || type === "hsl") {
        type += "a";
    }
    if (type === "color") {
        values[3] = `/${value}`;
    }
    else {
        values[3] = value;
    }
    return recomposeColor({ type, values });
}
/**
 * Darkens a color.
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color()
 * @param {number} coefficient - multiplier in the range 0 - 1
 * @returns {string} A CSS color string. Hex input values are returned as rgb
 */
export function darken(inputColor, coefficient) {
    const color = decomposeColor(inputColor);
    coefficient = clamp(coefficient);
    if (color.type.indexOf("hsl") !== -1) {
        color.values[2] *= 1 - coefficient;
    }
    else if (color.type.indexOf("rgb") !== -1 ||
        color.type.indexOf("color") !== -1) {
        for (let i = 0; i < 3; i += 1) {
            color.values[i] *= 1 - coefficient;
        }
    }
    return recomposeColor(color);
}
/**
 * Lightens a color.
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color()
 * @param {number} coefficient - multiplier in the range 0 - 1
 * @returns {string} A CSS color string. Hex input values are returned as rgb
 */
export function lighten(inputColor, coefficient) {
    const color = decomposeColor(inputColor);
    coefficient = clamp(coefficient);
    if (color.type.indexOf("hsl") !== -1) {
        color.values[2] += (100 - color.values[2]) * coefficient;
    }
    else if (color.type.indexOf("rgb") !== -1) {
        for (let i = 0; i < 3; i += 1) {
            color.values[i] += (255 - color.values[i]) * coefficient;
        }
    }
    else if (color.type.indexOf("color") !== -1) {
        for (let i = 0; i < 3; i += 1) {
            color.values[i] += (1 - color.values[i]) * coefficient;
        }
    }
    return recomposeColor(color);
}
/**
 * Darken or lighten a color, depending on its luminance.
 * Light colors are darkened, dark colors are lightened.
 * @param {string} color - CSS color, i.e. one of: #nnn, #nnnnnn, rgb(), rgba(), hsl(), hsla(), color()
 * @param {number} coefficient=0.15 - multiplier in the range 0 - 1
 * @returns {string} A CSS color string. Hex input values are returned as rgb
 */
export function emphasize(color, coefficient = 0.15) {
    return getLuminance(color) > 0.5
        ? darken(color, coefficient)
        : lighten(color, coefficient);
}
